import React from "react";
import "./footer.css";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import { FaLinkedin } from "react-icons/fa";
import { FaGithub } from "react-icons/fa";
import { IconContext } from "react-icons";
import { FaXingSquare } from "react-icons/fa";

const theme1 = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 16,
    textTransform: "capitalize"
  }
});

const theme3 = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 12,
    textTransform: "capitalize"
  }
});

const theme2 = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 28,
    textTransform: "capitalize",
    fontStyle: "italic",
    fontFamily: "Poppins, sans-serif"
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: `
        @font-face {
          font-family: 'Poppins';
          font-style: italic;
          font-display: swap;
          font-weight: 600;`
    }
  }
});

const Footer = () => {
  return (
    <footer>
      <h4>Here are my social media channels:</h4>
      <IconContext.Provider value={{ color: "white" }}>
        <>
          <a href="https://www.linkedin.com/in/rudolf-beck-2a169b224/">
            <FaLinkedin />
          </a>
        </>
        <a href="https://www.xing.com/profile/Rudolf_Beck17/cv">
          <FaXingSquare />
        </a>
        <a href="https://github.com/rudibrudi1997">
          <FaGithub />
        </a>
      </IconContext.Provider>

      <div className="footer_copyright">
        <ThemeProvider theme={theme3}>
          <Typography align="center" sx={{ mt: 3, color: "#55C4F3" }}>
            2022. Created by Rudolf Beck. All rights reserved.
          </Typography>
        </ThemeProvider>
      </div>
    </footer>
  );
};

export default Footer;
