import React from "react";
import "./contact.css";
import Button from "@mui/material/Button";
import SendIcon from "@mui/icons-material/Send";
import Typography from "@mui/material/Typography";

import { createTheme, ThemeProvider } from "@mui/material/styles";

const theme = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 18,
    textTransform: "capitalize"
  }
});
const theme2 = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 16,
    textTransform: "capitalize"
  }
});
const theme3 = createTheme({
  typography: {
    // Tell MUI what the font-size on the html element is.
    fontSize: 14,
    textTransform: "capitalize",
    color: "white",
    background: "black"
  }
});
const Contact = () => {
  return (
    <section className="cont_sec" id="contact">
      <div className="title">
        <h3>Contact</h3>
        <ThemeProvider theme={theme3}>
          <Typography
            variant="subtitle1"
            gutterBottom
            align="start"
            sx={{ mt: 4, ml: -2, mr: -5, mb: 5 }}
          >
            Do you have any further questions for me? You are welcome to ask
            your questions via the form. I am very much looking forward to your
            feedback.
          </Typography>
        </ThemeProvider>
      </div>
      <div className="container">
        <div className="contactFormular1">
          <Typography
            variant="h5"
            gutterBottom
            align="center"
            fontWeight="bold"
            sx={{ mt: 1, mb: 3 }}
          >
            Contact adresses:
          </Typography>
          <div className="box">
            <div className="boxIcon">
              <i class="fa-solid fa-location-pin"></i>
            </div>
            <div className="boxContent">
              <h4>Location</h4>
              <p>Rastatt, Germany</p>
            </div>
          </div>
          <div className="box">
            <div className="boxIcon">
              <i class="fa-solid fa-envelope"></i>
            </div>
            <div className="boxContent">
              <h4>Email</h4>
              <p>rudolf_beck@gmx.net</p>
            </div>
          </div>
          <div className="box">
            <div className="boxIcon">
              <i class="fa-solid fa-circle-info"></i>
            </div>
            <div className="boxContent">
              <h4>Availability</h4>
              <p>
                Monday - Friday
                <br />
                9am - 5pm
              </p>
            </div>
          </div>
        </div>
        <div className="contactFormular2">
          <form action="https://formspree.io/f/mvonovpn" method="POST">
            <h2>Write your message here: </h2>
            <h6>
              (Note: Sending mails in the contact form is done via "Formspree"){" "}
            </h6>
            <div className="inputBox">
              <input type="text" name="FName" required="required" />
              <span>First Name</span>
            </div>
            <div className="inputBox">
              <input type="text" name="LName" required="required" />
              <span>Last Name</span>
            </div>
            <div className="inputBox">
              <input type="email" name="Email" required="required" id="email" />
              <span className="Txt_1">E-Mail</span>
              <span id="txt_2"></span>
            </div>
            <div className="inputBox">
              <textarea required="required" name="Textarea"></textarea>
              <span>Write the message</span>
            </div>

            <Button variant="contained" type="submit" endIcon={<SendIcon />}>
              Send
            </Button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contact;
