import React from "react";
import "./techstack.css";
import Typography from "@mui/material/Typography";
import WebAssetIcon from "@mui/icons-material/WebAsset";
import Rating from "@mui/material/Rating";
import { Grid } from "@material-ui/core";
import { styled } from "@mui/material/styles";
import { createTheme, ThemeProvider } from "@mui/material/styles";

const TechStack = () => {
  return (
    <section className="tech" id="techs">
      <div className="title">
        <h3>Tech-Stack</h3>
        <Typography
          variant="subtitle1"
          gutterBottom
          align="start"
          sx={{ m: 3 }}
        >
          All knowledge from the different areas of the tech stack is listed
          here. Further details on the rest of the tech stack can be found in
          the resume.
        </Typography>
      </div>
      <div className="container">
        <h3>Frontend Development</h3>
        <div className="skills">
          <h4>HTML5 / CSS3</h4>
          <div className="rating">
            <Rating
              sm={4}
              name="read-only"
              value={5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>JS/TS</h4>
          <div className="rating">
            <Rating
              sm={4}
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>Angular</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={3.5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>React</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
      </div>
      <div className="container">
        <h3>Backend Development</h3>
        <div className="skills">
          <h4>Node.js / NPM</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>C# /.NET</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4.5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>Java, Kotlin</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={3.5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>Spring</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={3.5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
      </div>
      <div className="container">
        <h3>UX-/UI-Design / IDE / Tools</h3>
        <div className="skills">
          <h4>XD, Figma</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4.5}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>CodeSandbox</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>VS/VS Code</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{ color: "#093a83" }}
            />
          </div>
        </div>
        <div className="skills">
          <h4>Git, GitHub</h4>
          <div className="rating">
            <Rating
              name="read-only"
              value={4}
              precision={0.5}
              readOnly
              sx={{
                color: "#093a83",
                size: {
                  xs: "small",
                  sm: "small",
                  md: "medium",
                  lg: "large",
                  xl: "large"
                }
              }}
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default TechStack;
