import React from "react";
import "./nav.css";

const Nav = () => {
  return (
    <header>
      <h1 className="logo">Rudolf Beck</h1>
      <input type="checkbox" id="navToggle" className="navToggle" />
      <nav>
        <ul>
          <li>
            <a href="#home" className="active">
              Home
            </a>
          </li>
          <li>
            <a href="#about">About Me</a>
          </li>
          <li>
            <a href="#techs">Tech-Stack</a>
          </li>
          <li>
            <a href="#services">Services</a>
          </li>
          <li>
            <a href="#port">Portfolio</a>
          </li>
          <li>
            <a href="#contact">Contact</a>
          </li>
        </ul>
      </nav>
      <label for="navToggle" className="navLabel">
        <span></span>
      </label>
    </header>
  );
};

export default Nav;
